<template>
  <div class="user-login">
    <h1 class="title"></h1>
    <el-form :model="loginForm" status-icon :rules="rules" ref="loginForm">
      <div class="form-group">
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            placeholder="/"
            class="form-input-username"
          ></el-input>
        </el-form-item>

        <el-form-item prop="userpwd">
          <el-input
            type="password"
            v-model="loginForm.userpwd"
            placeholder=""
          ></el-input>
        </el-form-item>
      </div>
      <div class="form-group form-action row">
        <el-checkbox v-model="loginForm.rememberme"></el-checkbox>
        <router-link to="/user/findpwd"></router-link>
      </div>
      <div class="form-group form-btn">
        <el-button type="primary" size="medium" @click="submitForm"
          ></el-button
        >
      </div>
      <div class="form-link">
        
        <router-link to="/user/register"></router-link>
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  methods: {
    submitForm() {
      const self = this;
      self.$refs["loginForm"].validate(valid => {
        if (!valid) {
          return false;
        }
      });
    }
  },
  data() {
    var validateUserName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("/"));
      }
    };

    return {
      links: {
        register: "",
        findpwd: ""
      },
      loginForm: {
        username: "",
        userpwd: "",
        rememberme: false
      },
      rules: {
        username: [{ validator: validateUserName, trigger: "blur" }],
        userpwd: [{ required: true, message: "", trigger: "change" }]
      }
    };
  }
};
</script>
